
nav.menu ul {
    list-style: none;
    display: block;
    /*text-align:center;*/
    margin-top: 0.5rem;
    padding: 0;
    max-width: 45rem;
    /* Width is the same as tufte.css body */
    font-size: 1.1rem;
    width: 87.5%;
}

nav.menu li {
    display: block;
    margin-right: 1rem;
}

nav.menu li a {
    text-decoration: underline;
    background: transparent;
    color: inherit;
    text-transform: none;
}
